@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Cardo:wght@400&family=Raleway:wght@400;700&display=swap');

*{
    margin: 0 auto;
    padding: 0;
    text-decoration: none;
    list-style: none;
    color: inherit;
    z-index: 4;
}
.container_header{
    display: flex;
    max-width: 1200px;
    padding: 0, 15px;
    height: 100px;
    display: flex;
    border-bottom: 1px solid rgb(186, 187, 197);
    justify-content: space-between;
}
.front_page{
    background:url(bgs/img.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    position: relative;
}
.front_page::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(37, 43, 73);
    opacity: 0.8;
    z-index: 2;
}
.container{
    max-width: 1200px;
    padding: 0 15px;
    display: flex;
    z-index: 3;
}
/* .header_wrapper{
    display: flex;
    justify-content: space-between;
    width: 100%;
} */
.logo{
    display: flex;
    align-self: center;
}

.navigation{
    width: 50%;
    justify-content: space-between;
    padding: 0 10px;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(25, 23, 23);
    font-family: Raleway;
    font-size: 13px;
    font-weight: 700;
    line-height: 15px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    gap: 1px;
}
.nav{
width: calc(100%/5);
height: 100%;
justify-content: space-between;
display: flex;
color: rgb(255, 255, 255);
font-family: Raleway;
font-size: 13px;
font-weight: 700;
line-height: 15px;
letter-spacing: 0%;
text-align: center;
text-transform: uppercase;
align-items: center;
transition: 0.4s;
}
.nav:hover{
    border-bottom: 3px solid rgb(186, 187, 197);
}
.container_front{
    flex-direction: column;
    padding: 0 15px;
}
.main_head{
padding-top: 130px;
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
color: rgb(255, 255, 255);
font-family: Raleway;
font-size: 64px;
font-weight: 900;
line-height: 75px;
letter-spacing: 0%;
text-align: center;
text-transform: uppercase;
margin-bottom: 31px;
}
.main_text{
    padding: 0 15px;
    max-width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: center;
    margin-bottom: 80px;
}
.button{
    width: 370px;
    height: 80px;
    background-color: rgb(255, 67, 67);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 900;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    transition: 0.3s;
    margin-bottom: 120px;
}
.button:hover{
    scale: 120%;
}
.features{
    padding: 116px 0;
}
.container_features{
    padding: 0;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
.features_card{
    width: calc(100%/3 - 30px);
    display: flex;
    flex-direction: column;
    margin-bottom: 88px;
}
.card_img{
    margin-bottom: 30px;
}
.card_header{
    height: 72px;
    width: 100%;
    color: rgb(45, 48, 51);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 900;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
}
.card_filler{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(108, 114, 121);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
}
.works{
    margin: 0;
    padding: 0;
    width:100%;   
    display: flex;
    flex-wrap: wrap;
}
.works_img{
    position: relative;
    display: flex;
    width: calc(100% / 4);
}
.works_img__img{
    max-width: 100%;
    height: 340px;
    display: block;
    object-fit: cover;
    z-index: 1;
}
.works_img__content{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 67, 67, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: -1;
    opacity: 0;
    transition: 0.3s;
}

.works_img__content h3{
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 900;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 12px;
}
.works_img__content p {
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
}
.works_img:hover .works_img__content{
    z-index: 2;
    opacity: 1;
    cursor: pointer;
}
.our_team{
    padding-top: 114px;
    gap: 30px;
    justify-content: space-between;
    padding-bottom: 114px;
}
.team_card{
    width: calc(100% / 4 - 30px);
    display: flex;
    flex-direction: column;
}
.team_card_img{
    display: block;
    width: 100%;
    height: 100%;
   
}
.team_card_name {
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(45, 48, 51);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: capitalize;
}
.team_card_position{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(255, 67, 67);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: uppercase;
    margin-bottom: 18px;
}
.team_card_text{
    width: 100%;
    flex-direction: row;
    align-items: center;
    color: rgb(108, 114, 121);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: left;

}
.team_card_socials{
    margin: 0;
    margin-top: 38px;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 6px;
}
.team_card_socials__icon{
    margin: 0;
    padding: 0;
    display: block;
    width: 40px;
    height: 40px;
    transition: 0.3s;
}
.team_card_socials__icon:hover{
    scale: 1.1;
    fill: blue;
    cursor: pointer;
}

