.swiper {
    width: 100%;
    height: 535px;
    display: flex;
    flex-direction: row;
  }
.swiper-slide{
    display: flex;
    min-width: 0;
    width: 100%;
    height: 490px;
    background: rgb(92, 50, 73);
}
  .swiper-slide-img{
    margin: 0;
    display: block;
    width: 50%;
    height: 490px;
  }
  .swiper-slide-text{
    max-width: 471px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 130px 116px 170px 116px;
  }
  .swiper-slide-text p{
    max-width: 100%;
    color: rgb(255, 255, 255);
    font-family: Cardo;
    font-style: italic;
    font-size: 36px;
    font-weight: 400;
    line-height: 49px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: capitalize;
  }
  .swiper-slide-text-name{
    max-width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 13px;
    font-weight: 700;
    line-height: 15px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: uppercase;
  }

  .download {
    padding-top:116px;
    padding-bottom: 114px;
  }
  
  .container_2{
    max-width: 1200px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    z-index: 3;
}
  .download_header{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(0, 0, 0);
    font-family: Raleway;
    font-size: 30px;
    font-weight: 500;
    line-height: 35px;
    letter-spacing: 0%;
    text-align: center;
    margin-bottom: 30px;
  }
.download_text{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(0, 0, 0);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
}
.download_button{
    width: 370px;
    height: 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 24px;
    font-weight: 900;
    line-height: 28px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    background: rgb(255, 67, 67);
}
.footer{
    width: 100%;
    height: 340px;
    display: flex;
    flex-direction: column;
    background: rgb(53, 60, 101);;
}
.footer_card{
    margin-top: 72px;
    width: calc(100% / 3 - 30px);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: capitalize;
    margin-bottom: 70px;
}
.footer_card__header{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    font-family: Raleway;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 32px;
}
.copyright{
    width: 100%;
    height: 90px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: Raleway;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0%;
    text-align: center;
    text-transform: capitalize;
    background: rgb(44, 49, 84);
}
.copyright p{
    width: 370px;
}